<template>
  <div class="about">
    <h1>This is an about page</h1>
    <div class="trapezoid"></div>
    <div class="tranggler"></div>
    <p></p>
    <textarea class="cat-textarea" v-model="inputValue"></textarea>
    <div>DomPurify.sanitize: {{outputValue}}</div>
  </div>
</template>
<script>
import DomPurify from 'dompurify'
export default {
  data () {
    return {
      inputValue: '',
      outputValue: ''
    }
  },
  watch: {
    inputValue (val) {
      this.outputValue = DomPurify.sanitize(val)
    }
  }
}
</script>

